$wrapper-height: 500px;
body {
    overflow: hidden;
    background: url("../images/bg.jpeg") no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

#logo {
    position: relative;
    width: 100%;
    text-align: center;
    left: 0;
    
    h1 {
        color: white;
        font-size: 6.5em;
        margin-top: 60px;
        margin-bottom: 40px;
        text-shadow: 0 0px 6px rgba(133, 103, 92, 0.53);
        /* color: white; */
    }
    
}
#myobj {
    position:absolute;
    bottom: 0px;
    width:100px;
    margin-left: 160px;
    z-index:-1;
    -ms-transform: rotate(7deg); /* IE 9 */
    -webkit-transform: rotate(7deg); /* Chrome, Safari, Opera */
    transform: rotate(7deg);
}
#alert-agree {
    display: none;
    position:absolute;
    margin-left:20px;
    top: -5px;
}

#form-check-group {
    position:relative;
    margin-top:30px;
}
#i-agree-btn {
    margin-top: 30px;
}
#btn-wrapper {
    margin-top: 30px;
}
#btn-row {
    margin-top:30px;
}

#particles-js{
    width:100%;
    height:100%;
    position:fixed;
    z-index: -1;
}

#logo-row{
    margin-top: 70px;
    background-color: rgba(0,0,0,0.3);
}
#login {
    display: none;
    position: absolute;
    -moz-animation-duration: 1s;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
}
#content {
    position: relative;
    width: 600px;
    margin: 0 auto;
}

#agreement {
    display: none;
    position: absolute;
    text-align: left;
    -moz-animation-duration: 1s;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
}

#wrapper {
    position: fixed;
    text-align: center;
    width: 100%;
    left: 0;
    height: 500px;
    top: 50%;
    margin-top: -320px;
}
#login-error {
    display: none;
}
